@media (max-width: 767px) {
  .subheader {
    &_logo {
      img {
        width: 100%; } } }
  .uber_img {
      margin: -190px; }
  .menu {
    position: fixed;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    top: 0;
    left: -100%;
    width: 50%;
    height: 100vh;
    padding: 20px 15px;
    background-color: rgba(0,0,0, .8);
    transition: 0.6s all;
    &_active {
      left: 0; }
    &_item {
      padding-right: 0;
      border: none;
      margin: 10px 0; }
    &_link {
      font-size: 20px; } }
  .hamburger {
    display: block;
    position: absolute;
    right: 20px;
    top: 25px;
    transform: translateY(-50%);
    width: 26px;
    height: 12px;
    span {
      display: block;
      height: 2px;
      width: 100%;
      background-color: #fff;
      margin-bottom: 4px;
      transition: 0.6s all; }
    &_active {
      span {
        margin-bottom: -2px;
        &:nth-child(1) {
          transform: translateY(3px) rotate(-45deg); }
        &:nth-child(2) {
          display: none; }
        &:nth-child(3) {
          transform: translateY(3px) rotate(45deg); } } } }
  .xs-hidden {
